* {
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  height: 100vh;
  background-color: #AEC3AE;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'DM Sans', sans-serif;
}

h1 {
  margin-bottom: .8em;
  color: #454545;
}

form {
  padding: 1em;
  width: 90%;
  max-width: 500px;
  background-color: #E4E4D0;
  border-radius: .2em;
  display: flex;
  justify-content: center;
  margin-bottom: 1em;
}

input {
  padding: .5em;
  width: 70%;
  border: 2px solid #454545;
  border-right: 0;
  outline: none;
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  font-size: 1rem;
  color: #454545;
}

button {
  background-color: #AEC3AE;
  padding: 1em;
  color: #fff;
  font-size: .9rem;
  font-weight: 500;
  border: 2px solid #454545;
  border-left: 0;
  font-family: 'DM Sans', sans-serif;
}

button:hover {
  background-color: #94A684;
}

.main-content {
  padding: 1em 1em;
  background-color: #E4E4D0;
  width: 90%;
  max-width: 500px;
  border-radius: .2em;
  text-align: center;
}